import { configureStore, createSlice } from '@reduxjs/toolkit'

const cart = createSlice({
    name: 'kim cart',
    initialState: [
        // state.cart의 값들
    ],
    // 수정할려면?
    reducers: {
        subCount(state, action) {
            if (state[action.payload].count > 0) {
                state[action.payload].count--
            }
        },
        pluCount(state, action) {
            state[action.payload].count++
        },
        addItem(state, action) {
            // 상품을 눌렀을 때 중복이면 수량만 ++
            let find = state.find(x => x.id == action.payload.id)
            if (find == undefined) {
                state.push(action.payload)
            } else {
                find.count++
            }
        },
        deleteItem(state, action) {
            // 상품 삭제
            let i = state.find(x => x.id == action.payload.id)
            state.splice(i, 1)
        },
        // totalprice(state, action) {
        // 총가격
        // }
    }
})
// 수정하는것들 내보낼려면?
export let { subCount, pluCount, addItem, deleteItem } = cart.actions

export default configureStore({
    reducer: {
        cart: cart.reducer
    }
}) 