import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Pagination, Navigation } from "swiper";
import ReactPlayer from 'react-player'

function Home() {
    return (
        <div className='main-bg'>
            <div className='bg-left'>
                <Swiper
                    direction={"vertical"}
                    spaceBetween={30}
                    centeredSlides={true}
                    autoplay={{
                        delay: 100,
                        disableOnInteraction: false,
                    }}
                    pagination={{
                        clickable: true,
                    }}
                    modules={[Autoplay, Pagination, Navigation]}
                    className="mySwiper"
                >
                    <SwiperSlide>
                        <ReactPlayer className="video"
                            url={process.env.PUBLIC_URL + '/video/video3.mp4'}
                            width='100%'
                            height='100%'
                            playing={true}
                            muted={true}
                            loop={true}
                        />
                    </SwiperSlide>
                </Swiper>
            </div>

            <div className='bg-center'>
                <h4 className="bg-center-text">
                    <span className='bg-text1'>Hand Ma</span>
                    <span className='bg-text2'>de Mon</span>
                </h4>
            </div>

            <div className='bg-right'>
                <Swiper
                    direction={"vertical"}
                    spaceBetween={30}
                    centeredSlides={true}
                    autoplay={{
                        delay: 1500,
                        disableOnInteraction: false,
                    }}
                    modules={[Autoplay, Pagination, Navigation]}
                    className="mySwiper">
                    <SwiperSlide>
                        <img src={`${process.env.PUBLIC_URL}/img/main3.jpg`} alt="img3" />
                    </SwiperSlide>
                    <SwiperSlide>
                        <img src={`${process.env.PUBLIC_URL}/img/main2.jpg`} alt="img2" />
                    </SwiperSlide>
                    <SwiperSlide>
                        <img src={`${process.env.PUBLIC_URL}/img/main1.jpg`} alt="img1" />
                    </SwiperSlide>
                </Swiper>
            </div>
        </div >
    )
}

export default Home