import { Table } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import { pluCount, subCount, deleteItem } from '../store/store'
function Cart() {
    let state = useSelector((state) => { return state })
    let dispatch = useDispatch()
    return (<>
        <div className='table-container'>
            <Table>
                <thead className='cart-head'>
                    <tr>
                        <th>ID</th>
                        <th>카테고리</th>
                        <th>제품명</th>
                        <th>가격</th>
                        <th>수량</th>
                        <th>삭제</th>
                        <th>금액</th>
                    </tr>
                </thead>
                <tbody className='cart-list'>
                    {
                        state.cart.map((a, i) =>
                            <tr key={i} >
                                <td>{state.cart[i].id}</td>
                                <td>{state.cart[i].category}</td>
                                <td>{state.cart[i].title}</td>
                                <td>{state.cart[i].price}</td>
                                <td>
                                    <a className='cart-amount' onClick={() => {
                                        dispatch(subCount(i))
                                    }}> - </a>
                                    <span>{state.cart[i].count}</span>
                                    <a className='cart-amount' onClick={() => {
                                        dispatch(pluCount(i))
                                    }}> + </a>
                                </td>
                                <td>
                                    <a className='cart-delete' onClick={() => {
                                        dispatch(deleteItem(i))
                                    }}>X</a>
                                </td>
                                <td>
                                    {state.cart[i].price * state.cart[i].count}
                                </td>
                            </tr>
                        )
                    }
                </tbody>
            </Table >
        </div>

        <div className='total-box'>
            총 금액 : <span>{state.cart.reduce((total, cart) => total + (cart.price * cart.count), 0).toLocaleString()}원
            </span></div>
    </>
    )
}

export default Cart