import { Link } from 'react-router-dom';

function Nav_bar() {

    return (
        <nav>
            <div className='nav-logo'>
                <Link to='/' className='logo'><span>H</span>MM</Link>
            </div>
            <div className='navbar'>
                <Link to='/top' className='navlist' >Top</Link>
                <Link to='/Pants' className='navlist'>Pants</Link>
                <Link to='/Shoes' className='navlist'>Shoes</Link>
                <Link to='/Cart' className='navlist'>Cart</Link>
            </div>
        </nav>
    )
}

export default Nav_bar