import { useState } from 'react'
import axios from 'axios'
import shoes_data from '../datas/shoes_data'

function Shoes(props) {
    const [shoes, setshoes] = useState(shoes_data)
    const [btn, setBtn] = useState(true)

    const axios_btn = () => {
        axios
            .get('https://gist.githubusercontent.com/kyonam2/2c6c9f81b72f69aef6210ad068088d85/raw/a201303985bae6846e4760f9590979599befe7d1/data.json')
            .then((res) => {
                const shoesfilter = res.data.filter((item) => (item.category === 'Shoes'))
                setshoes(shoesfilter);
                setBtn(false);
            });
    }

    return (
        <div className='shoes-container'>
            {shoes.map((item, i) => {
                return (
                    <ul key={i}>
                        <li >
                            <div className='shoes-img'>
                                <img src={`${process.env.PUBLIC_URL}/img/${i + 18}.jpg`}
                                    onClick={() => { props.navigate('/detail/' + (i + 18)) }} />
                            </div>
                            <div className='shoes-text'>
                                <p>{item.category}</p>
                                <h4>{item.title}</h4>
                                <h2>￦{item.price.toLocaleString()}</h2>
                            </div>
                        </li>
                    </ul>
                )
            })}
            {btn == true ?
                <button className='shoes-btn' onClick={axios_btn}>View More →</button> : null
            }
        </div>
    )
}
export default Shoes