import { useState } from 'react'
import axios from 'axios'
import top_data from '../datas/top_data'

function Top(props) {
    const [top, setTop] = useState(top_data)
    const [btn, setBtn] = useState(true)

    const axios_btn = () => {
        axios
            .get('https://gist.githubusercontent.com/kyonam2/2c6c9f81b72f69aef6210ad068088d85/raw/a201303985bae6846e4760f9590979599befe7d1/data.json')
            .then((res) => {
                const topfilter = res.data.filter((item) => (item.category === 'Top'))
                setTop(topfilter);
                setBtn(false);
            });
    }

    return (
        <div className='top-container'>
            {top.map((item, i) => {
                return (
                    <ul key={i}>
                        <li >
                            <div className='top-img'>
                                <img src={`${process.env.PUBLIC_URL}/img/${i}.jpg`}
                                    onClick={() => { props.navigate('/detail/' + i) }} />
                            </div>
                            <div className='top-text'>
                                <p>{item.category}</p>
                                <h4>{item.title}</h4>
                                <h2>￦{item.price.toLocaleString()}</h2>
                            </div>
                        </li>
                    </ul>
                )
            })}
            {btn == true ?
                <button className='top-btn' onClick={axios_btn}>View More →</button> : null
            }
        </div>
    )
}
export default Top