import { ImHome, ImPhone, ImMap, ImGoogle, ImGithub, ImFacebook2, ImInstagram, ImTwitter } from "react-icons/im";

function footer(props) {
    return (
        <footer className='footer'>
            <div className="footer-container">
                <div className="footer-logo">
                    <img src={`${process.env.PUBLIC_URL}/img/shop.png`}></img>
                </div>
                <div className="footer-right">
                    <div className='footer-text'>
                        <li> <ImHome /> : Hand Made Mon</li>
                        <li> <ImPhone /> : 010-0000-0000</li>
                        <li> <ImMap /> : 서울특별시 관악구 신림동</li>
                        <li> <ImGoogle /> : abcd1234@google.com</li>
                    </div>
                    <div className="footer-icon">
                        <li> <ImGithub /> </li>
                        <li> <ImFacebook2 /></li>
                        <li> <ImInstagram /> </li>
                        <li> <ImTwitter /></li>
                    </div>
                </div>
            </div >
        </footer>
    )
}

export default footer