import { useState } from 'react'
import axios from 'axios'
import pants_data from '../datas/pants_data'

function Pants(props) {
    const [pants, setPants] = useState(pants_data)
    const [btn, setBtn] = useState(true)

    const axios_btn = () => {
        axios
            .get('https://gist.githubusercontent.com/kyonam2/2c6c9f81b72f69aef6210ad068088d85/raw/a201303985bae6846e4760f9590979599befe7d1/data.json')
            .then((res) => {
                const pantsfilter = res.data.filter((item) => (item.category === 'Pants'))
                setPants(pantsfilter);
                setBtn(false);
            });
    }

    return (
        <div className='pants-container'>
            {pants.map((item, i) => {
                return (
                    <ul key={i}>
                        <li >
                            <div className='pants-img'>
                                <img src={`${process.env.PUBLIC_URL}/img/${i + 9}.jpg`}
                                    onClick={() => { props.navigate('/detail/' + (i + 9)) }} />
                            </div>
                            <div className='pants-text'>
                                <p>{item.category}</p>
                                <h4>{item.title}</h4>
                                <h2>￦{item.price.toLocaleString()}</h2>
                            </div>
                        </li>
                    </ul>
                )
            })}
            {btn == true ?
                <button className='pants-btn' onClick={axios_btn}>View More →</button> : null
            }
        </div>
    )
}
export default Pants