import { useNavigate } from "react-router-dom"
import axios from "axios";
import { useEffect, useState } from "react";

function Product() {
  let navigate = useNavigate()
  const [products, setProducts] = useState([]);

  useEffect(() => {
    axios
      .get('https://gist.githubusercontent.com/kyonam2/2c6c9f81b72f69aef6210ad068088d85/raw/a201303985bae6846e4760f9590979599befe7d1/data.json')
      .then((res) => {
        setProducts(res.data)
      }).catch(() => {
        console.log('실패')
      })
  }, [])


  return (
    <section className="product">
      <h2 className='header'>Product</h2>
      <div className="product-container">
        {products.map((product, i) => (
          <li key={i}>
            <div className="product-img">
              <img src={`${process.env.PUBLIC_URL}/img/${i}.jpg`} alt="img"
                onClick={() => { navigate('/detail/' + i) }} />
            </div>
            <div className="product-text">
              <h1>{product.category}</h1>
              <h1>{product.title} </h1>
              <h1>￦{product.price.toLocaleString()} </h1>
            </div>
          </li>
        ))}
      </div>
    </section>
  )
}
export default Product