import { useParams } from 'react-router-dom';
import { addItem } from '../store/store.js'
import { useDispatch } from 'react-redux';
import data from '../datas/data.js'

function Detail() {
    const all = data;

    const { id } = useParams();
    const findItem = all.find(x => x.id == id);
    const dispatch = useDispatch();

    return (
        <section className='detail'>
            <div className='detail-container'>
                <div className='detail-img'>
                    <img src={`${process.env.PUBLIC_URL}/img/${[id]}.jpg`} />
                </div>
                <div className='detail-content'>
                    <div className='detail-text'>
                        <p>{findItem.category}</p>
                        <h2>{findItem.title}</h2>
                        <h3>￦{findItem.price.toLocaleString()}</h3>
                    </div>
                    <button className="detail-btn" onClick={() => {
                        alert('장바구니에 상품이 담겼습니다.')
                        dispatch(addItem(
                            {
                                id: findItem.id,
                                category: findItem.category,
                                title: findItem.title,
                                price: findItem.price,
                                count: 1
                            }
                        ))
                    }}>장바구니</button>
                    <hr></hr>
                    <p> 본 제품은 예약구매로 진행되며, 배송 기간이 약 2주에서 최대 3달까지 소요될 수 있습니다. 제품 예약 기능 수명 주기가 만료 되면 주문이 취소됩니다. 세부 안내를 참조 바랍니다.</p>
                </div>
            </div>
        </section>
    )
}

export default Detail